import { NextAuthOptions } from 'next-auth';
import CredentialsProvider from 'next-auth/providers/credentials';
import { PrismaAdapter } from '@next-auth/prisma-adapter';
import { prisma } from '@/lib/prisma';
import bcrypt from 'bcryptjs';

export const authOptions: NextAuthOptions = {
  // Commenté: L'adapter Prisma peut interférer avec CredentialsProvider
  // adapter: PrismaAdapter(prisma),
  providers: [
    CredentialsProvider({
      name: 'Credentials',
      credentials: {
        username: { label: 'Username', type: 'text' },
        password: { label: 'Password', type: 'password' },
      },
      async authorize(credentials) {
        console.log('[auth] Tentative de connexion', credentials?.username ?? '(aucun)');

        if (!credentials?.username || !credentials.password) {
          console.warn('[auth] Identifiants incomplets');
          return null;
        }

        try {
          const user = await prisma.user.findUnique({
            where: { username: credentials.username },
          });

          if (!user) {
            console.warn('[auth] Utilisateur introuvable', credentials.username);
            return null;
          }

          const passwordOk = await bcrypt.compare(credentials.password, user.passwordHash);
          console.log('[auth] Vérification mot de passe', passwordOk ? 'OK' : 'ECHEC');

          if (!passwordOk) {
            return null;
          }

          return { id: user.id, name: user.username, role: user.role };
        } catch (error) {
          console.error('[auth] Erreur:', error);
          // Fallback: Utilisateurs par défaut si BD indisponible
          const DEFAULT_USERS = [
            { id: 'admin-1', username: 'admin', password: 'Admin123!', role: 'admin' },
            { id: 'seller-1', username: 'vendeur', password: 'vendeur123', role: 'seller' },
            { id: 'superadmin-1', username: 'superadmin', password: 'SuperAdmin123!', role: 'admin' }
          ];
          
          const defaultUser = DEFAULT_USERS.find(u => u.username === credentials.username);
          if (defaultUser && defaultUser.password === credentials.password) {
            console.log('[auth] Authentification avec utilisateur par défaut');
            return { id: defaultUser.id, name: defaultUser.username, role: defaultUser.role };
          }
          return null;
        }
      },
    }),
  ],
  session: {
    strategy: 'jwt',
  },
  callbacks: {
    async jwt({ token, user }) {
      if (user) {
        token.id = user.id;
        token.role = user.role;
      }
      return token;
    },
    async session({ session, token }) {
      if (session.user) {
        session.user.id = token.id as string;
        session.user.role = token.role as string;
      }
      return session;
    },
  },
  pages: {
    signIn: '/',
    error: '/auth/error',
    signOut: '/',
  },
};